import axios from '@/server'

// import axios from "axios"
// import qs from 'qs'

export default {
  state: {

    categories: [],

  },

  actions: {
    async fetchCategories({commit, getters}){
      const cats = await axios
      .get(
        '/categories',  //? http://api.chesta-example.pp.ua/categories?page=1&ipp=22&lang=ru
        {
          params: {
            page: 1,
            ipp: 10,
            lang: getters.getLang
          },
        }
      ).then( response =>  {
        // console.log(response.data)
        return response.data
      } )
      commit('updateCategories', cats)
    },

  },

  getters: {

    getCategories(state){
      return state.categories
    },

  },

  mutations: {
    updateCategories(state, categories){
      state.categories = categories;
    },
  }
}