import axios from '@/server'
import qs from 'qs'

export default {

  state: {
    allNewPosts: [],
    totalPages: null,
    currentPage: null,
  },

  actions: {
    async fetchAllNewPosts({commit, getters}, {page, per_page}){
      const posts = await axios
        .get(
          '/games',
          {
            headers: {
              'Accept': 'application/ld+json',
            },
            params: {
              page: page || 1,
              ipp: per_page || 11,
              order: {
                updatedAt: 'desc'
              },
              lang: getters.getLang
            },
            paramsSerializer: params => {
              return qs.stringify(params)
            }
          }
        )
        .then(
          response => {
            return response.data
          }
        )
        .catch(error => {
          this.dispatch("SHOW_GLOBAL_ERROR_MESSAGE", error.response.data.message)
        })

        const totalPages = Math.ceil( posts["hydra:totalItems"] / per_page)

        commit("updateAllNewPosts", posts['hydra:member'])
        commit("updateTotalPages", totalPages)
        commit("updateCurrentPage", page)
    } 
  },

  getters: {
    getAllNewPosts(state){
      return state.allNewPosts
    },

    getTotalPages(state, getters){
      let excessLeft = Number(getters.getCurrentPage) - 3 //! 3 - it's ( range + 2 )
      let excessRight = state.totalPages - (Number(getters.getCurrentPage) + 2) //! 2 - it's ( range + 1 )

      let pagesArr = [...Array(state.totalPages).keys()]
      if(excessLeft > 1){
        pagesArr.splice(1, excessLeft, '...')
      }
      if(excessRight > 1){
        pagesArr.splice(pagesArr.length - excessRight - 1, excessRight, '...')
      } 

      return pagesArr
    },

    getCurrentPage(state){
      return state.currentPage
    }
    
  },

  mutations: {
    updateAllNewPosts(state, posts){
      state.allNewPosts = posts
    },
    updateTotalPages(state, pages){
      state.totalPages = pages
    },
    updateCurrentPage(state, page){
      state.currentPage = page
    }
    
  },


}

