<script>

import Slick from 'vue-slick';
import 'slick-carousel/slick/slick.css';
import $ from 'jquery'


export default {
  props: {
    header: Boolean,
  },

  data(){
    return {
      topHeader: this.header,
      tagsOptions: {
        slidesToShow: 6,
        slidesToScroll: 2,
        rows: 2,
        infinite: true,
        // centerMode: true,
        variableWidth: true,
        prevArrow: `<button class="prev_arrow tagsSlick" ref="tagsPrevArrow">
          <svg width="15" height="24" viewBox="0 0 15 24" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M2.82 0L0 2.82L9.16 12L0 21.18L2.82 24L14.82 12L2.82 0Z" fill="white"/>
          </svg>
        </button>`,
        nextArrow: `<button class="next_arrow tagsSlick" ref="tagsNextArrow">
          <svg width="15" height="24" viewBox="0 0 15 24" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M2.82 0L0 2.82L9.16 12L0 21.18L2.82 24L14.82 12L2.82 0Z" fill="white"/>
          </svg>
        </button>`,
        responsive: [
          {
            breakpoint: 1899,
            settings: {
              variableWidth: true,
              slidesToShow: 1,
              slidesToScroll: 1,
              infinite: true,
              rows: 2
            }
          },
        ]
      }
    }
  },
  components: {Slick},

  methods: {
    getRandomArbitrary(min, max) {
      return Math.random() * (max - min) + min;
    },
    handleBeforeChange(event, slick, currentSlide, nextSlide) {
      if (nextSlide == 0){
        $('.prev_arrow.tagsSlick').hide();
      } else {
        $('.prev_arrow.tagsSlick').show();
      }
    },
    handleInit() {
      $('.prev_arrow.tagsSlick').hide();
    },
  }
}
</script>

