import axios from '@/server'


export default {
  state: {
    rec_posts: [],
  },
  getters: {
    getRecPosts(state){
      return state.rec_posts;
    }
  },
  actions: {
    async fetchRecPosts({commit, getters}){
      var qs = require('qs');
      const posts = await axios
        .get(
          '/games',
          {
            params: {
              page: 1,
              ipp: 22,
              order: {
                rating: 'desc'
              },
              lang: getters.getLang
            },
            paramsSerializer: params => {
              return qs.stringify(params)
            }
          }
        )
        .then( response => {
            return response.data;
          } 
        )
        commit('updateRecPosts', posts);

    }
  },
  mutations: {
    updateRecPosts(state, posts){
      state.rec_posts = posts;
    }
  },
}