<template>
  <section id="folderPrev">
    <div class="folderPrev">
      <div class="folderPrev-header">
        <h2>{{ title }}</h2>
        <router-link :to="'/' + slug" >View more</router-link>
      </div>
      <div v-if="posts.length === 11" class="folderPrev-content">
        <slick class="slider" ref="folderPrevSlick" 
          :options="folderPrevOptions"
          @beforeChange="folderPrevBeforeChange"
          @init="folderPrevInit"
          > 
          <div v-for="post in posts" :key="post.id" class="folderPrev-content__item">
            <img :src="post.img" :alt="post.name">
            <div class="folderPrev-content__item-title">
              <p>{{ titleNormalizeLenght(post.name) }}</p>
            </div>
            <router-link :to="'/' + post.id"></router-link>
          </div>

        </slick>
      </div>
      <FoldersPreviewSkeletonVue v-else />
    </div>
  </section>
</template>

<script>

import Slick from 'vue-slick';
import 'slick-carousel/slick/slick.css';
import $ from 'jquery'
import axios from '@/server'
import FoldersPreviewSkeletonVue from '../skeletons/FoldersPreviewSkeleton.vue';


// import { mapActions } from 'vuex';


export default {
  data(){
    return {
      posts: [],
      catSlug: this.slug,
      slickArrows: {
        nextArrow: '.next_arrow.' + this.catSlug + 'Slick',
        prevArrow: '.prev_arrow.' + this.catSlug + 'Slick'
      },
      folderPrevOptions: {
        slidesToShow: 11,
        slidesToScroll: 2,
        rows: 1,
        infinite: true,
        // centerMode: true,
        variableWidth: true,
        prevArrow: `<button class="prev_arrow ${this.catSlug}Slick" ref="catPrePrevArrow">
          <svg width="15" height="24" viewBox="0 0 15 24" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M2.82 0L0 2.82L9.16 12L0 21.18L2.82 24L14.82 12L2.82 0Z" fill="white"/>
          </svg>
        </button>`,
        nextArrow: `<button class="next_arrow ${this.catSlug}Slick" ref="catPreNextArrow">
          <svg width="15" height="24" viewBox="0 0 15 24" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M2.82 0L0 2.82L9.16 12L0 21.18L2.82 24L14.82 12L2.82 0Z" fill="white"/>
          </svg>
        </button>`,
        responsive: [
          {
            breakpoint: 1899,
            settings: {
              variableWidth: true,
              slidesToShow: 10,
              slidesToScroll: 2,
              infinite: true,
              rows: 1
            }
          },
          {
            breakpoint: 576,
            settings: {
              slidesToShow: 5,
              slidesToScroll: 2,
              infinite: true,
              rows: 1
            }
          },
        ]
      }

    }
  },

  props: {
    title: {required: true, type: String},
    slug: {required: true, type: String},
  },

  components: {
    Slick,
    FoldersPreviewSkeletonVue
  },

  async mounted(){
    // await this.fetchGamesForCategoryPreview({ categorySlug: this.catSlug })

    this.posts = await axios.get(
        '/categories/' + this.slug + '/games',
        {
          params: {
            page: 1,
            ipp: 11,
            // order: {
            //   rating: 'asc'
            // },
            lang: 'ru'
          }
        }
      ).then( response => {
        // console.log(fetchUrl)
        // console.log(response.data)
        return response.data
      } )

    },

  computed: {
    // ...mapGetters([
    //   'getGamesForCategoryPreview'
    // ])
  },

  methods: {

    titleNormalizeLenght(string){
      if(string.length > 30){
        return string.slice(0, 30) + '...'
      } else {
        return string
      }
    },

    // ...mapActions([
    //   'fetchGamesForCategoryPreview', //also supports payload `this.nameOfAction(amount)` 
    // ]),

    folderPrevBeforeChange(event, slick, currentSlide, nextSlide) {
      if (nextSlide == slick.$slides.length-12){
        $(this.slickArrows.nextArrow).hide();
      } else {
        $(this.slickArrows.nextArrow).show();
      }
      if (nextSlide == 0){
        $(this.slickArrows.prevArrow).hide();
      } else {
        $(this.slickArrows.prevArrow).show();
      }
    },
    folderPrevInit() {
      $(this.slickArrows.prevArrow).hide();
    },
    

  },

}
</script>

<style lang="sass" scoped>
.folderPrev
  &-header
    border-radius: 8px 8px 0 0
    background-color: $green
    height: 65px
    display: flex
    align-items: center
    justify-content: space-between
    & h2 
      padding: 0 30px
      margin: 0
      color: $almost_white
      font-family: Montserrat
      font-style: normal
      font-weight: 600
      font-size: 22px
      line-height: 22px
    & a 
      padding: 15px 30px
      color: $dark_green
      font-family: Montserrat
      font-style: normal
      font-weight: bold
      font-size: 18px
      line-height: 22px
      text-decoration: none
  &-content
    width: 100%
    padding: 15px 0
    &__item
      position: relative
      width: 156px !important
      display: flex !important
      border-radius: 8px
      overflow: hidden
      cursor: pointer
      &:hover
        & .folderPrev-content__item-title
          bottom: 0
      & img 
        object-fit: cover
        width: 100%
        height: 100%
      &-title
        position: absolute
        width: 100%
        bottom: -90px
        padding: 10px
        background-color: $title_hover
        border-bottom-left-radius: 8px
        border-bottom-right-radius: 8px
        transition: .4s all ease
        & p
          text-align: center
          padding: 0
          margin: 0
          font-family: Montserrat
          font-style: normal
          font-weight: 500
          font-size: 14px
          line-height: 16px
          color: $light_green
      & a 
        position: absolute
        width: 100%
        height: 100%
.slick-slide 
  min-height: 117px !important
.slider, .folderPrev-content
  min-height: 117px

// @media screen and (max-width: 575.98px)
//   .folderPrev
//     &-content
//       padding-left: 25px

@media screen and (max-width: 575.98px)
  .slider, .folderPrev-content
    margin-left: 0 
    margin-right: 0
    min-height: 112.5px
  .folderPrev-content__item
    width: 150px !important

@media screen and (max-width: 479.98px)
  .folderPrev
    &-header
      & h2 
        font-size: 18px
        padding: 0px 10px
      & a 
        font-size: 14px
        padding: 0px 10px
</style>