<template>
  <section id="popTags">
    <div v-if="tags.length === 12" class="popTags recs">
      <div class="popTags-header" v-show="topHeader">
        <h2>Popular Tags</h2>
        <router-link to="/tags" >View more</router-link>
      </div>
      <div class="popTags-content">
        <slick v-if="tags.length" class="slider" ref="tagsSlick"
          :options="tagsOptions"
          @beforeChange="handleBeforeChange"
          @init="handleInit"
        >
          <div v-for="tag in tags" :key="tag.id" class="popTags-content__item">
            <div class="popTags-content__item-icon"><img :src="tag.img" :alt="tag.name"></div>
            <div class="popTags-content__item-title">
              <p>{{ titleNormalizeLenght(tag.name) }}</p>
            </div>
            <router-link :to="'/' + tag.id"></router-link>
          </div>
        </slick>
      </div>
    </div>
    <PopularTagsSkeletonVue v-else />
  </section>
</template>

<script>

import Slick from 'vue-slick';
import 'slick-carousel/slick/slick.css';
import $ from 'jquery'
import PopularTagsSkeletonVue from '../skeletons/PopularTagsSkeleton.vue';




export default {
  props: {
    header: Boolean || true,
    tags: {
      required: true,
      type: Array,
    }
  },

  data(){
    return {
      topHeader: this.header,
      tagsOptions: {
        slidesToShow: 6,
        slidesToScroll: 2,
        rows: 2,
        infinite: true,
        // centerMode: true,
        variableWidth: true,
        prevArrow: `<button class="prev_arrow tagsSlick" ref="tagsPrevArrow">
          <svg width="15" height="24" viewBox="0 0 15 24" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M2.82 0L0 2.82L9.16 12L0 21.18L2.82 24L14.82 12L2.82 0Z" fill="white"/>
          </svg>
        </button>`,
        nextArrow: `<button class="next_arrow tagsSlick" ref="tagsNextArrow">
          <svg width="15" height="24" viewBox="0 0 15 24" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M2.82 0L0 2.82L9.16 12L0 21.18L2.82 24L14.82 12L2.82 0Z" fill="white"/>
          </svg>
        </button>`,
        responsive: [
          {
            breakpoint: 1899,
            settings: {
              variableWidth: true,
              slidesToShow: 1,
              slidesToScroll: 1,
              infinite: true,
              rows: 2
            }
          },
        ]
      }
    }
  },
  components: {
    Slick,
    PopularTagsSkeletonVue
  },

  methods: {
    titleNormalizeLenght(string){
      if(string.length > 40){
        return string.slice(0, 40) + '...'
      } else {
        return string
      }
    },
    handleBeforeChange(event, slick, currentSlide, nextSlide) {
      if (nextSlide == 0){
        $('.prev_arrow.tagsSlick').hide();
      } else {
        $('.prev_arrow.tagsSlick').show();
      }
    },
    handleInit() {
      $('.prev_arrow.tagsSlick').hide();
    },
  }
}
</script>

<style lang="sass" scoped>
#popTags
  display: flex
  justify-content: center
.popTags
  // max-width: 1861px
  width: 100%
  &-header
    border-radius: 8px 8px 0 0
    background-color: $yellow
    height: 65px
    display: flex
    align-items: center
    justify-content: space-between
    & h2 
      padding: 0 30px
      margin: 0
      color: $almost_white
      font-family: Montserrat
      font-style: normal
      font-weight: 600
      font-size: 22px
      line-height: 22px
    & a 
      padding: 15px 30px
      color: $dark_green
      font-family: Montserrat
      font-style: normal
      font-weight: bold
      font-size: 18px
      line-height: 22px
      text-decoration: none
  &-content
    margin-top: 15px
    min-height: 130px
    width: 100%
    & .slick-slide:not(:last-child)
      margin-right: 15px
    & .slick-list
      // margin-right: -15px
    &__item
      width: 298px !important
      position: relative
      display: flex !important
      border-radius: 8px
      overflow: hidden
      background-color: $green
      height: 50px
      margin-bottom: 15px
      transition: .4s all ease   
      &:hover
        background-color: $dark_green
        & .popTags-content__item-title p
          color: $white
      &-icon
        width: 50px
        height: 50px
        background-color: #fff
        & img
          width: 100%
          height: 100%
          object-fit: cover 
      &-title
        display: flex
        align-items: center
        width: calc(100% - 50px)
        & p
          font-family: Montserrat
          font-style: normal
          font-weight: bold
          font-size: 14px
          padding: 0 20px
          margin: 0
          color: $light_green
      & a 
        position: absolute
        width: 100%
        height: 100%
        z-index: 1
.slider
  min-height: 130px

// @media screen and (max-width: 575.98px)
//   .popTags
//     &-content
//       padding-left: 25px

@media screen and (max-width: 575.98px)
  .slider
    margin-left: 0
    margin-right: 0
  .popTags-content__item
    width: 250px !important
@media screen and (max-width: 479.98px)
  .popTags
    &-header
      & h2 
        font-size: 18px
        padding: 0px 10px
      & a 
        font-size: 14px
        padding: 0px 10px
</style>