<template>
  <section id="pagination">
    <div class="pagination"> <!-- Active class on <li> for active page -->
      <ul>
        <li class="prev"><router-link :to="'/page/' + (Number(getCurrentPage) - 1)">&lt;</router-link></li>
        <li 
          v-for="(page, index) of getTotalPages" 
          :key="index" @click="fetchAllNewPosts({page: Number(page) + 1, per_page: 11})"
        >
        <!-- {{index}}{{page}} -->
          <router-link v-if="Number(page) === 0"
            :to="'/'"
            :class="'active'"
          >
            {{ Number(page) + 1 }}
          </router-link>
          <router-link v-else
            :to="'/page/' + (Number(page) + 1)"
            >
              {{ page === '...' ? '...' : Number(page) + 1 }}
            </router-link>
        </li>
        <li class="next"><router-link :to="'/page/' + (Number(getCurrentPage) + 1)">&gt;</router-link></li>
      </ul>
    </div>
  </section>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'


export default {
  data(){
    return {
      pages: null
    }
  },
  props: {
  },
  methods: {
    ...mapActions(['fetchAllNewPosts']),
  },
  mounted(){
  },
  created(){
  },
  computed: {
    ...mapGetters(['getCurrentPage', 'getTotalPages']),
  },


  
}
</script>

<style lang="sass">

.pagination
  padding: 30px 0 
  display: flex
  align-items: center
  justify-content: center
  & ul 
    display: flex
    list-style: none
    padding-inline-start: 0
    & li 
      margin: 0 10px
      width: 35px
      height: 35px
      display: flex
      align-items: center
      justify-content: center
      border-radius: 50%
      background-color: $green
      & .router-link-active
        background-color: $pink
        color: $almost_white
      & .router-link-exact-active
        background-color: $pink
        color: $almost_white
      &.active 
        background-color: $pink
        color: $almost_white
      & a 
        width: 100%
        height: 100%
        display: flex
        justify-content: center
        align-items: center
        border-radius: 50%
        font-family: Montserrat
        font-style: normal
        font-weight: bold
        font-size: 14px
        line-height: 16px
        color: $light_green
        // background-color: $green
        text-decoration: none
      &.next
        & a 
          color: $dark_green
          background-color: $white
      &.prev
        & a 
          background-color: $white
          color: $dark_green
</style>