// import axios  from "@/server"

import axios from "@/server"

export default {

  state: {
    popularTags: [],
  },

  actions: {

    async fetchPopularTags({commit, getters}){
      const popTags = await axios
        .get(
          '/tags', //? http://api.chesta-example.pp.ua/tags?page=1&ipp=12&lang=ru
          {
            params: {
              page: 1,
              ipp: 12,
              lang: getters.getLang,
            }
          }
        )
        .then(response => {
          return response.data
        })
        .catch(error => {
          this.dispatch("SHOW_GLOBAL_ERROR_MESSAGE", error.response.data.message)
        })
        // console.log(popTags)
        commit('updatePopularTags', popTags)
    }
  
  },

  getters: {
    getPopularTags(state){
      return state.popularTags;
    }
  },

  mutations: {
    updatePopularTags(state, popularTags){
      state.popularTags = popularTags;
    }
  },


}

