<template>
  <section id="recomended">
    <div v-if="getRecPosts.length === 22" class="recomended recs">
      <div class="recomended-header">
        <h2>{{ title }}</h2>
      </div>
      <div class="recomended-content">
        <slick class="slider" ref="recSlick" 
          :options="recOptions"
          @beforeChange="handleBeforeChange"
          @init="handleInit"
          > 
          <div v-for="post in getRecPosts" :key="post.id" class="recomended-content__item">
            <img :src="post.img" :alt="post.name">
            <div class="recomended-content__item-title">
              <p>{{ titleNormalizeLenght(post.name) }}</p>
            </div>
            <router-link :to="'/game/' + post.id"></router-link>
          </div>
        </slick>
      </div>
    </div>
    <RecomendedSkeletonVue v-else />
  </section>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'

import Slick from 'vue-slick';
import 'slick-carousel/slick/slick.css';
import $ from 'jquery'
import RecomendedSkeletonVue from './RecomendedSkeleton.vue';


export default {

  data(){
    return {
      recOptions: {
        slidesToShow: 11,
        slidesToScroll: 4,
        rows: 2,
        infinite: false,
        // centerMode: true,
        variableWidth: true,
        prevArrow: `<button class="prev_arrow recSlick" ref="recPrevArrow">
          <svg width="15" height="24" viewBox="0 0 15 24" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M2.82 0L0 2.82L9.16 12L0 21.18L2.82 24L14.82 12L2.82 0Z" fill="white"/>
          </svg>
        </button>`,
        nextArrow: `<button class="next_arrow recSlick" ref="recNextArrow">
          <svg width="15" height="24" viewBox="0 0 15 24" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M2.82 0L0 2.82L9.16 12L0 21.18L2.82 24L14.82 12L2.82 0Z" fill="white"/>
          </svg>
        </button>`,
        responsive: [
          {
            breakpoint: 1899,
            settings: {
              variableWidth: true,
              slidesToShow: 6,
              slidesToScroll: 2,
              infinite: true,
              rows: 2
            }
          },
          {
            breakpoint: 576,
            settings: {
              variableWidth: true,
              slidesToShow: 5,
              slidesToScroll: 2,
              infinite: true,
              rows: 2
            }
          },
        ]
      }
    }
  },

  components: {
    Slick,
    RecomendedSkeletonVue
  },
  props: {
    title: {
      required: true,
      type: String,
    },
  },
  methods: {
    ...mapActions(['fetchRecPosts']),
    titleNormalizeLenght(string){
      if(string.length > 30){
        return string.slice(0, 30) + '...'
      } else {
        return string
      }
    },
    handleBeforeChange(event, slick, currentSlide, nextSlide) {

      // if (nextSlide == slick.$slides.length-12){
      //   $('.next_arrow.recSlick').hide();
      // } else {
      //   $('.next_arrow.recSlick').show();
      // }
      if (nextSlide == 0){
        $('.prev_arrow.recSlick').hide();
      } else {
        $('.prev_arrow.recSlick').show();
      }
    },
    handleInit() {
      $('.prev_arrow.recSlick').hide();
    },
  },
  computed: {
    ...mapGetters(["getRecPosts"])
  },
  async mounted(){
    await this.fetchRecPosts()
  }

  
}
</script>

<style lang="sass" scoped>

.recomended
  &-header
    border-radius: 8px 8px 0 0
    background-color: $dark_green
    height: 65px
    display: flex
    align-items: center
    & h2 
      padding: 0 30px
      margin: 0
      color: $almost_white
      font-family: Montserrat
      font-style: normal
      font-weight: 600
      font-size: 22px
      line-height: 22px
  &-content
    width: 100%
    margin-top: 15px
    &__item
      width: 155.6px !important
      position: relative
      display: flex !important
      border-radius: 8px
      height: 117px
      margin-bottom: 15px
      overflow: hidden
      cursor: pointer
      &:hover
        & .recomended-content__item-title
          bottom: 0
      & img 
        object-fit: cover
        width: 100%
        height: 100%
      &-title
        position: absolute
        width: 100%
        bottom: -90px
        padding: 10px
        background-color: $title_hover
        border-bottom-left-radius: 8px
        border-bottom-right-radius: 8px
        transition: .4s all ease
        & p
          text-align: center
          padding: 0
          margin: 0
          font-family: Montserrat
          font-style: normal
          font-weight: 500
          font-size: 14px
          line-height: 16px
          color: $light_green
      & a 
        position: absolute
        width: 100%
        height: 100%

.slick-track
  width: 100%

.slick-slider
  position: relative

.recomended-content, .slider
  min-height: 264px
  
  
// @media screen and (max-width: 575.98px)
//   .recomended
//     &-content
//       padding-left: 25px
@media screen and (max-width: 1024px)
  .recomended-content__item-title
    bottom: 0
    & p 
      width: 143px
      white-space: nowrap
      overflow: hidden
      text-overflow: ellipsis

@media screen and (max-width: 575.98px)
  .slider, .recomended-content
    margin-left: 0 
    margin-right: 0
    min-height: 255px

  .recomended-content__item
    width: 150px !important
@media screen and (max-width: 479.98px)
  .recomended-header
    & h2 
      font-size: 18px
</style>